<template>
  <el-dialog v-model="store.CourseListDialogData.ifShow"
             :title="`Á¨¨ ${store.CourseListDialogData.week} Âë®`" :fullscreen="false" width="95%">

    <fast-add-course-dialog></fast-add-course-dialog>

    <div class="dialogBody">
      <div class="TimeBlock">
        <div class="TimeBlockHead">ÊòüÊúü‰∏Ä</div>
        <div class="TimeBlockHead">ÊòüÊúü‰∫å</div>
        <div class="TimeBlockHead">ÊòüÊúü‰∏â</div>
        <div class="TimeBlockHead">ÊòüÊúüÂõõ</div>
        <div class="TimeBlockHead">ÊòüÊúü‰∫î</div>
        <div class="TimeBlockHead">ÊòüÊúüÂÖ≠</div>
        <div class="TimeBlockHead">ÊòüÊúüÊó•</div>

        <template v-for="row in 5" :key="row">
          <template v-for="col in 7" :key="col">
            <div class="TimeBlockItem">
              <template v-if="getData[`${row},${col}`].length">
                <div class="CourseCard" v-for="course in getData[`${row},${col}`]"
                     :key="course.course_id" :style="{backgroundColor: '#' + course.color}"
                     @dblclick="redirect(getUrl.courseChangePage(course.course_id))">
                  <!--                <div>row:{{ row }},col:{{ col }}</div>-->
                  <div v-show="course.ch_name">{{ course.ch_name }}</div>
                  <div v-show="course.en_name">{{ course.en_name }}</div>
                  <div v-show="course.fr_name">{{ course.fr_name }}</div>
                  <div v-show="course.date">{{ course.date }}</div>
                  <div v-show="course.method">{{ course.method }}</div>
                  <div v-show="course.teacher">{{ course.teacher }}</div>
                  <div v-show="course.groups">{{ course.groups }}</div>
                  <div v-show="course.room">{{ course.room }}</div>
                  <div v-show="course.note">{{ course.note }}</div>
                  <!--                <div>which_lesson: {{ course.data.which_lesson }}</div>-->
                  <!--                <div>what_day: {{ course.what_day }}</div>-->
                </div>
              </template>
              <template v-else>
                <div class="DbClickHint" @dblclick="fastAddCourse(row, col)">
                  ÂèåÂáªÊ≠§Â§ÑÊù•Ê∑ªÂä†ËØæÁ®ã
                </div>
              </template>
            </div>
          </template>
        </template>
      </div>
    </div>
  </el-dialog>
</template>


<script setup>
import dayjs from "dayjs";

import {useCounterStore} from "@/store/counter";
import {computed} from "vue";
import getUrl from "@/assets/urls";
import FastAddCourseDialog from "./FastAddCourseDialog";

const store = useCounterStore();

function Course(_data) {
  this.data = _data;
  this.course_id = _data.course_id;
  this.date = _data.date;
  this.ch_name = _data.plan.info.ch_name;
  this.en_name = _data.plan.info.en_name;
  this.fr_name = _data.plan.info.fr_name;
  this.method = _data.plan.method;
  this.color = _data.plan.info.type.color;
  this.teacher = _data.plan.teacher.name;
  this.note = _data.note;
  let _groups = [];
  for (let _group of _data.plan.groups) {
    _groups.push(_group.name);
  }
  this.groups = _groups.join("&");
  this.room = _data.room.name;
  this.what_day = (new Date(_data.date)).getDay();
  this.which_lesson = _data.which_lesson;
}

const getData = computed(() => {
  let data = [];
  for (let i = 1; i < 6; i++) {
    for (let j = 1; j < 8; j++) {
      data[`${i},${j}`] = [];
    }
  }
  for (const courseData of store.CourseListDialogData.courses) {
    let _course = new Course(courseData);
    let timeBlockIndex = `${_course.which_lesson},${_course.what_day}`;
    data[timeBlockIndex].push(_course);
  }
  return data;
});

const redirect = (url) => {
  window.location.href = url;
  // window.open(url);
};

const fastAddCourse = (row, col) => {
  // let firstWeekMonday = dayjs(store.semesterConfig.week1_monday_date);
  // let dateThisWeekMonday = firstWeekMonday.add(store.CourseListDialogData.week - 1, "week");
  // let date = dateThisWeekMonday.add(col - 1, "day");
  store.FastAddCourseDialogData.what_day = col;
  store.FastAddCourseDialogData.which_lesson = row;
  store.FastAddCourseDialogData.ifShow = true;
};
</script>

<style scoped>
.TimeBlock {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  color: black;
}

.TimeBlockHead, .TimeBlockItem {
  width: 14%;
  border: black 1px solid;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
}

.TimeBlockItem {
  height: 150px;
  cursor: pointer;
  user-select: none;
}

.CourseCard, .DbClickHint {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.TimeBlockItem .DbClickHint {
  color: white;
}

.TimeBlockItem:hover, .DbClickHint:hover, .CourseCard:hover {
  border-radius: 20px;
  border: 1px solid red;
  color: black;
}
</style>