<template>
  <el-dialog v-model="store.FastAddCourseDialogData.ifShow"
             :title="`ËØ∑ÈÄâÊã©ÊïôÂÆ§`"
             :fullscreen="false" width="95%">
    <h3>{{ apiData.timeBlocks[0].date }} Á¨¨{{ apiData.timeBlocks[0].which_lesson }}ËäÇËØæ</h3>
    <el-select v-if="apiData.classrooms" v-model="data.selectedClassroom" class="m-2"
               placeholder="ÈÄâÊã©ÊïôÂÆ§">
      <el-option
          v-for="item in apiData.classrooms"
          :key="item.room_id"
          :label="item.name"
          :value="item.room_id"
      >
      </el-option>
    </el-select>

    <div class="CommitButton">
      <el-button type="success" :disabled="data.selectedClassroom===undefined" round @click="addCoursePost">Á°ÆÂÆöÊ∑ªÂä†</el-button>
    </div>
  </el-dialog>
</template>

<script setup>
import {useCounterStore} from "@/store/counter";
import getUrl from "@/assets/urls";

const store = useCounterStore();
import {watch, reactive} from "vue";

const data = reactive({
  selectedClassroom: undefined,
});

const apiData = reactive({
  classrooms: null,
  timeBlocks: null,
});

const getClassroomData = () => {
  let _weeks = [store.CourseListDialogData.week];
  let what_days = [store.FastAddCourseDialogData.what_day];
  let which_lessons = [store.FastAddCourseDialogData.which_lesson];
  let _url = getUrl.advancedApiClassroom(_weeks, what_days, which_lessons);
  store.axiosMethod(_url,
      response => {
        if (response.data.results) {
          apiData.timeBlocks = response.data.timeBlocks;
          apiData.classrooms = response.data.results;
        }
      },
  );

};

watch(() => store.FastAddCourseDialogData.ifShow, (newStatus) => {
  if (newStatus) {
    getClassroomData();
    data.selectedClassroom = undefined;
  }
});

const addCoursePost = () => {
  store.axiosAddCoursePost({
    plan: store.CourseListDialogData.plan_id, room: data.selectedClassroom,
    date: apiData.timeBlocks[0].date, which_lesson: apiData.timeBlocks[0].which_lesson,
  }, () => store.myReset());
};
</script>

